@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap");
:root {
    --primary: #6200ee;
    --primaryBox: #6200ee50;
    --primaryVariant: #3700b3;
    --secondary: #03dac6;
    --secondaryVariant: #018786;
    --background: #ffffff;
    --x-background: #121212;
    --text: #000000;
    --projectBackground: #d1d1d1;
}
* {
    margin: 0;
    padding: 0;
}

.fullbody {
    background: var(--background);
    font-family: "Lato", sans-serif;
    min-height: 100vh;
    position: relative;
}

.cursor {
    position: fixed;
    border-radius: 50%;
    box-shadow: 0 0 60px 60px var(--text);
    background: var(--text);
    transform: translate(-50%, -50%);
}

.left,
.right {
    position: absolute;
    height: 100%;
    background: var(--background);
}

.left {
    left: 0;
}

.right {
    top: 0;
    right: 0;
}

.bottom {
    background: var(--background);
}

.main {
    max-width: 1024px;
    position: relative;
    margin: 0 auto;
}

#color {
    display: none;
    &:checked ~ .fullbody .main .nav-bar span label::after {
        content: "Light";
    }
    &:checked ~ .fullbody {
        --primary: #bb86fc;
        --primaryBox: #bb86fc50;
        --primaryVariant: #3700b3;
        --secondary: #03dac6;
        --secondaryVariant: #03dac6;
        --background: #121212;
        --x-background: #ffffff;
        --text: #ffffff;
        --projectBackground: #ececec;
    }
}

.nav-bar {
    background: var(--background);
    height: 41px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
    .nav-link {
        padding: 0 10px;
        position: relative;
        left: 0;
        &,
        a {
            color: var(--text);
            text-decoration: none;
            font-size: 16px;
            text-transform: uppercase;
        }
        span {
            margin: 0 5px;
            display: none;
        }
        label {
            cursor: pointer;
            &::after {
                content: "Dark";
            }
        }
        .nav-link-active {
            color: var(--primary);
        }
    }
}

@mixin projectdesign($name) {
    transition: 0.5s linear;
    display: flex;
    .remaining-#{$name} {
        background: var(--background);
        border: 1px solid rgba(255, 255, 255, 0);
        outline: 11px solid var(--background);
        margin: 10px;
    }
    .#{$name} {
        position: relative;
        transition: 0.5s;
        border: 1px solid rgba(0, 0, 255, 0.021);
        outline: 11px solid var(--background);
        margin: 10px;
        min-width: 234px;
        height: 300px;
        &:hover {
            transition: 0.5s;
            min-width: 490px;
        }
        .#{$name}-container {
            transition: 0.25s ease;
            position: relative;
            height: 100%;
            background-color: var(--background);
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            border-radius: 5px;
            &:hover{
                .#{$name}-name{
                    display: initial;
                }
            }
            .#{$name}-name {
                width: 100%;
                font-weight: 700;
                padding: 3px 10px;
                letter-spacing: 2px;
                color: var(--text);
                text-transform: uppercase;
                font-size: 18px;
                text-align: center;
                position: absolute;
                bottom: 0;
                background: var(--background);
                opacity: 0.75;
                display: none;
            }
        }
    }
}

.Projects {
    position: relative;
    z-index: 2;
    .projects {
        overflow-x: hidden;
        .project-row {
            @include projectdesign(project);
            @mixin project($num, $img1, $img2) {
                .project#{$num} {
                    .project-container {
                        background-image: url($img1);
                        &:hover {
                            transition: 0.5s;
                            background: url($img2) center;
                            background-size: cover;
                        }
                    }
                }
            }
            @include project(1, "images/passwordmobile.png", "images/passworddesktop.png");
            @include project(2, "images/pokemonmobile.png", "images/pokemondesktop.png");
            @include project(3, "images/bird.jpg", "images/birds.jpg");
            @include project(4, "images/bird.jpg", "images/birds.jpg");
            @include project(5, "images/bird.jpg", "images/birds.jpg");
            @include project(6, "images/bird.jpg", "images/birds.jpg");
        }
    }
}

.Designs {
    position: relative;
    z-index: 2;
    .designs {
        overflow-x: hidden;
        .design-row {
            @include projectdesign(design);
            @mixin design($num, $img1, $img2) {
                .design#{$num} {
                    .design-container {
                        background-image: url($img1);
                        &:hover {
                            transition: 0.5s;
                            background: url($img2) center;
                            background-size: cover;
                        }
                    }
                }
            }
            @include design(1, "images/bird.jpg", "images/birds.jpg");
            @include design(2, "images/bird.jpg", "images/birds.jpg");
            @include design(3, "images/bird.jpg", "images/birds.jpg");
            @include design(4, "images/bird.jpg", "images/birds.jpg");
            @include design(5, "images/bird.jpg", "images/birds.jpg");
            @include design(6, "images/bird.jpg", "images/birds.jpg");
        }
    }
}

.design-row-hover {
    transition: 0.5s linear;
    transform: translate(-256px, 0);
}

.Home {
    background: var(--background);
    font-family: "Roboto", sans-serif;
    position: relative;
    .home-container1 {
        position: relative;
        padding: 0 10px;
        height: calc(100vh - 51px);
        letter-spacing: 2px;
        display: flex;
        align-items: center;
        .extrahome,
        .extrahome1 {
            position: absolute;
            border-radius: 15px;
            background: var(--primary);
            top: 50%;
            transform: translateY(-50%) rotateZ(45deg);
        }
        .extrahome {
            left: 95px;
            height: 155px;
            width: 155px;
            opacity: 0.2;
        }
        .extrahome1 {
            right: 75px;
            height: 250px;
            width: 250px;
            opacity: 0.3;
        }
        div {
            h1 {
                font-size: 40px;
                font-weight: 800;
                line-height: 55px;
                color: var(--text);
                .primary {
                    color: var(--primary);
                }
            }
            h6 {
                font-size: 12px;
                font-weight: 100;
                color: var(--text);
            }
        }
        .contact {
            position: absolute;
            bottom: 20px;
            left: 10px;
            img {
                width: 15px;
                height: 15px;
                margin: 0 10px;
            }
        }
        .scroll-text {
            position: absolute;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
            width: 100px;
            height: 30px;
            border-radius: 15px;
            background: var(--primary);
            display: flex;
            align-items: center;
            padding-left: 20px;
            p {
                display: flex;
                & > span {
                    margin: 0 5px;
                    display: flex;
                    flex-direction: column;
                    position: relative;
                    span {
                        position: absolute;
                        font-size: 12px;
                        margin: -5px 0;
                        font-weight: 700;
                        transform: scaleX(1.5);
                        &:nth-child(1) {
                            top: 0;
                            animation: scroll-text 2s linear infinite;
                        }
                        &:nth-child(2) {
                            top: 10px;
                            animation: scroll-text 2s linear 1s infinite;
                        }
                    }
                }
            }
        }
    }
    .home-container2 {
        min-height: 100vh;
        font-family: "Lato", sans-serif;
        overflow: hidden;
        @mixin section($pos, $name, $left, $rel, $shdw) {
            transition: 0.5s;
            position: relative;
            #{$left}: 0;
            height: 250px;
            width: 500px;
            background: var(--x-background);
            border: 1px solid transparent;
            margin: 30px 50px;
            float: left;
            border-radius: 10px;
            color: var(--background);
            display: flex;
            @media screen and (max-width: 620px) {
                height: 325px;
                width: 300px;
                flex-direction: column;
            }
            &:hover {
                transition: 0.5s;
                background: none;
                border: 1px solid var(--primary);
                color: var(--text);
            }
            .#{$name}-fixed {
                display: flex;
                flex-direction: column;
                position: $rel;
                top: 30px;
                #{$pos}: -30px;
                border-radius: 10px;
                width: 190px;
                min-height: 190px;
                max-height: 190px;
                background: var(--primary);
                box-shadow: $shdw 5px 5px var(--primaryBox);
                display: flex;
                align-items: center;
                justify-content: center;
                @media screen and (max-width: 620px) {
                    top: -30px;
                    #{$pos}: 55px;
                }
                span img {
                    width: 130px;
                    height: 130px;
                    color: var(--text);
                }
                h1 {
                    font-size: 20px;
                    font-weight: 900;
                }
            }
            input {
                display: none;
            }
        }
        @mixin labels($pos) {
            height: 60px;
            width: 8px;
            position: absolute;
            #{$pos}: 10px;
            top: 50%;
            transform: translateY(-50%);
            label {
                transition: 0.3s;
                display: block;
                cursor: pointer;
                margin: 5px 0;
                height: 8px;
                width: 8px;
                background: var(--primary);
                border-radius: 4px;
                box-shadow: 0 0 5px 2px var(--primaryBox);
            }
        }
        @mixin containers {
            width: 250px;
            height: 100%;
            display: flex;
            align-items: center;
            overflow: hidden;
            @media screen and (max-width: 620px) {
                align-items: flex-start;
                margin: 0 10px;
            }
        }
        @mixin head {
            font-size: 50px;
            font-weight: 900;
            margin: auto;
            position: relative;
            p {
                color: var(--text);
                text-align: center;
            }
        }
        .education-main {
            position: relative;
            width: 100%;
            height: auto;
            display: flex;
            justify-content: flex-start;
            .education-section {
                @include section(left, education, left, relative, 5px);
                @mixin edu-labels($id, $contenth4, $contentp, $contentli, $animationh4, $animationp, $animationli) {
                    ##{$id}:checked {
                        & ~ .education-labels .education-#{$id} {
                            transition: 0.3s;
                            height: 20px;
                        }
                        & ~ .education-containers .education-container h4::after {
                            content: $contenth4;
                            position: relative;
                            animation: $animationh4 0.35s linear 1;
                        }
                        & ~ .education-containers .education-container p::after {
                            content: $contentp;
                            position: relative;
                            animation: $animationp 0.7s linear 1;
                        }
                        & ~ .education-containers .education-container ul li::after {
                            content: $contentli;
                            position: relative;
                            animation: $animationli 1s linear 1;
                        }
                    }
                }
                @include edu-labels(
                    college,
                    "Pursuing BE Mechanical",
                    "Institute of Engineering And Technology, Indore",
                    "2019 - 2023",
                    educationClgh4,
                    educationClgp,
                    educationClgli
                );

                @include edu-labels(
                    highschool,
                    "High School ( 79.3% )",
                    "Bundelkhand Public Higher Secondary School Hatta, Damoh,MP",
                    "2016 - 2017",
                    educationHsh4,
                    educationHsp,
                    educationHsli
                );

                @include edu-labels(
                    intermediate,
                    "Intermediate ( 73.2% )",
                    "Bundelkhand Public Higher Secondary School Hatta, Damoh,MP",
                    "2018 - 2019",
                    educationItmh4,
                    educationItmp,
                    educationItmli
                );

                .education-labels {
                    @include labels(right);
                }
                .education-containers {
                    @include containers();
                    & > .education-container {
                        h4 {
                            font-size: 22px;
                            font-weight: 700;
                        }
                        p {
                            font-size: 14px;
                            font-weight: 500;
                            margin: 25px 0 10px 0;
                        }
                        ul {
                            font-size: 10px;
                            font-weight: 300;
                            list-style: none;
                        }
                    }
                }
            }
            .education-head {
                @include head();
            }
        }

        .head-main {
            position: relative;
            width: 100%;
            height: auto;
            display: flex;
            justify-content: flex-end;
            .head-section {
                @include section(right, head, right, absolute, -5px);
                .head-labels {
                    @include labels(left);
                }
                .head-containers {
                    @include containers();
                    margin: 0 50px;
                    @media screen and (max-width: 620px) {
                        position: absolute;
                        top: 200px;
                    }
                }
            }
            .head-head {
                @include head();
            }
        }

        .skill-main {
            .skill-heading {
                padding: 20px;
                h2 {
                    color: var(--text);
                    font-weight: 900;
                    text-align: center;
                }
            }
            .skill-section {
                display: flex;
                justify-content: center;
                .skill-containers {
                    display: flex;
                    flex-wrap: wrap;
                    padding: auto;
                    .skill-container {
                        margin: 20px;
                        width: 200px;
                        height: 200px;
                        &:hover {
                            .skill-box {
                                transition: 0.5s;
                                background: none;
                                border: 1px solid var(--primary);
                                transform: rotateY(180deg);
                                .skill-text,
                                .skill-img {
                                    display: none;
                                }
                                .skill-rating {
                                    transition: 0.5s;
                                    display: initial;
                                    color: var(--text);
                                    transform: rotateY(180deg);
                                }
                            }
                        }
                        .skill-box {
                            position: relative;
                            width: 200px;
                            height: 200px;
                            background: var(--text);
                            border: 1px solid transparent;
                            box-sizing: border-box;
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            border-radius: 10px;
                            .skill-img {
                                width: 120px;
                                height: 120px;
                                border-radius: 50%;
                                margin: 20px 0;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                                background: var(--primary);
                                box-shadow: 0 0 5px 5px var(--primaryBox);
                                img {
                                    width: 75px;
                                    height: 75px;
                                }
                            }
                            .skill-text {
                                color: var(--background);
                                p {
                                    font-weight: 900;
                                    font-size: 20px;
                                }
                            }
                            .skill-rating {
                                display: none;
                                color: var(--background);
                                text-align: center;
                                margin: auto 10px;
                            }
                        }
                    }
                }
            }
        }
    }
}

@mixin education-animation($h4, $p, $li) {
    @keyframes #{$h4} {
        0% {
            left: -200px;
            opacity: 0;
        }
        100% {
            left: 0;
            opacity: 1;
        }
    }
    @keyframes #{$p} {
        0% {
            left: -200px;
            opacity: 0;
        }
        100% {
            left: 0;
            opacity: 1;
        }
    }
    @keyframes #{$li} {
        0% {
            left: -200px;
            opacity: 0;
        }
        100% {
            left: 0;
            opacity: 1;
        }
    }
}

@include education-animation(educationClgh4, educationClgp, educationClgli);
@include education-animation(educationHsh4, educationHsp, educationHsli);
@include education-animation(educationItmh4, educationItmp, educationItmli);

@keyframes fixed {
    0% {
        left: -200px;
    }
}

@keyframes S-fixed {
    0% {
        right: -200px;
    }
}

@keyframes fixed-img {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}

@keyframes scroll-text {
    from {
        top: -4px;
        opacity: 0;
    }
    to {
        top: 12px;
        opacity: 1;
    }
}

@keyframes skill-box {
    from {
        transform: scale(0.1);
    }
    to {
        transform: scale(1);
    }
}
